<template>
  <div id="app">
    <section class="u-align-center u-clearfix u-section-2" id="sec-dba4">
      <div class="u-clearfix u-sheet u-sheet-1">
        <h2 class="u-align-center u-text u-text-default u-text-1 priceT">Актуальная таблица <img src="img/excel.png" width="60px" title="Скачать таблицу" @click="downloadTable" style="cursor: pointer; font-family: Arial, sans-serif"></h2>
        <div class="u-expanded-width u-table u-table-responsive u-table-1">
          <table class="u-table-entity u-table-entity-1">
            <colgroup>
              <col width="25%">
              <col width="25%">
              <col width="25%">
              <col width="25%">
            </colgroup>
            <thead class="u-palette-4-base u-table-header u-table-header-1" @click="hideTable">
            <tr style="height: 25px; text-align: center; font-size: 18px">
              <th class="u-border-1 u-border-palette-3-base u-gradient u-table-cell u-table-cell-1">Название</th>
              <th class="u-border-1 u-border-palette-4-base u-gradient u-table-cell u-table-cell-2">Остаток</th>
              <th class="u-border-1 u-border-palette-4-base u-gradient u-table-cell u-table-cell-3">Оптовая цена</th>
              <th class="u-border-1 u-border-palette-4-base u-gradient u-table-cell u-table-cell-4">Розничная цена</th>
            </tr>
            </thead>
            <tbody class="u-table-body">
            <tr v-for="(wheel) in wheels" :key="wheel.id" :id="wheel.id" style="height: 45px; text-align: center">
              <td class="u-border-1 u-border-grey-30 u-table-cell">{{wheel.name}}</td>
              <td class="u-border-1 u-border-grey-30 u-table-cell">{{wheel.portion}}</td>
              <td class="u-border-1 u-border-grey-30 u-table-cell">{{wheel.optPrice}}</td>
              <td class="u-border-1 u-border-grey-30 u-table-cell">{{wheel.price}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import wheels from '@/data/wheels'

export default {
  data() {
    return {
      showRowsAll: true,
    };
  },
  computed: {
    wheels() {
      let currentTable = wheels;
      return currentTable;
    }
  },
  methods: {
    downloadTable() {
      let csvContent = 'Название,Партия,Оптовая цена,Розничная цена\n';
      this.wheels.forEach((wheel) => {
        const row = `${wheel.name},${wheel.portion},${wheel.optPrice},${wheel.price}\n`;
        csvContent += row;
      });

      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });

      const downloadLink = document.createElement('a');
      const url = URL.createObjectURL(blob);
      downloadLink.setAttribute('href', url);
      downloadLink.setAttribute('download', 'table.csv');
      downloadLink.style.display = 'none';

      document.body.appendChild(downloadLink);
      downloadLink.click();

      document.body.removeChild(downloadLink);
      URL.revokeObjectURL(url);
    },
    hideTable() {
      let table = document.querySelectorAll("tbody tr");
      if (this.showRowsAll) {
        table.forEach((elem, index) => {
          if (index >= 10) {
            elem.style.display = "none";
          }
        });
        this.showRowsAll = false;
      } else {
        table.forEach((elem, index) => {
          elem.style.display = "";
        });
        this.showRowsAll = true;
      }
    }
  }

}
</script>
